import dateView from "../view/dateView";
import monthView from "../view/monthView";
import inputView from "../view/inputView";
import { h12Convert } from "../util/calendar";
import { dataKey, getData } from "../store";
import { saveData } from "../util/fetchUtil";

export const beforeDate = () => {
  const targetDate = getData(dataKey.targetDate);
  targetDate.setDate(targetDate.getDate() - 1);
  dateView(targetDate);
};

export const beforeMonth = () => {
  const targetDate = getData(dataKey.targetDate);
  targetDate.setMonth(targetDate.getMonth() - 1);
  monthView(targetDate);
};

export const cancel = () => {
  const targetDate = getData(dataKey.targetDate);
  monthView(targetDate);
};

export const confirm = () => {
  const targetDate = getData(dataKey.targetDate);
  saveData(
    {},
    () => {},
    () => {}
  );
  monthView(targetDate);
};

export const input = (e) => {
  const { datestr, type, hour24str } = e.target.dataset;

  switch (type) {
    case "month": {
      inputView({
        start: { date: datestr, time: "0 0" },
        end: { date: datestr, time: "24 0" },
      });
      break;
    }
    case "date": {
      const nextTime = parseInt(hour24str) + 1;
      inputView({
        start: { date: datestr, time: `${h12Convert(hour24str)}` },
        end: {
          date: datestr,
          time: `${h12Convert(nextTime >= 24 ? 0 : nextTime)}`,
        },
      });
      break;
    }
  }
};

export const menuDate = () => {
  const targetDate = getData(dataKey.targetDate);
  dateView(targetDate);
};

export const menuMonth = () => {
  const targetDate = getData(dataKey.targetDate);
  monthView(targetDate);
};

export const menuWeek = () => {
  const targetDate = getData(dataKey.targetDate);
  monthView(targetDate);
};

export const nextDate = () => {
  const targetDate = getData(dataKey.targetDate);
  targetDate.setDate(targetDate.getDate() + 1);
  dateView(targetDate);
};

export const nextMonth = () => {
  const targetDate = getData(dataKey.targetDate);
  targetDate.setMonth(targetDate.getMonth() + 1);
  monthView(targetDate);
};
